<template>
  <div class="pageContent">
    <el-card class="filter-container" shadow="never">
      <div class="pageHead flex-row-between">
        <div>店铺余额充值</div>
        <el-button type="text" @click="$router.push({path:'/assetsManage/account/record',query:{type:1}})">查看充值记录</el-button>
      </div>
      <el-form ref="form" :model="form" label-width="160px">
        <el-form-item label="账户可用余额：">
          <div class="warningFont fs40">{{shopAsset}}</div>
        </el-form-item>
        <el-form-item label="充值金额：">
          <el-input-number 
            v-model="rechargeForm.money" 
            :controls="false"
            step-strictly
            :min="1"
            :max="10000">
          </el-input-number> 元
          <span class="dangerFont" style="margin-left:10px;">只能是0以上正整数，单次金额不可超过1万</span>
        </el-form-item>
        <el-form-item label="充值方式：">
          <el-radio-group v-model="form.resource">
            <el-radio label="1">微信</el-radio>
            <el-radio label="2" disabled>支付宝</el-radio>
          </el-radio-group>
          <span>（目前仅支持扫码支付）</span>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="onSubmit">确定充值</el-button>
        </el-form-item>
      </el-form>
    </el-card>
    <el-card class="filter-container mt20" shadow="never">
      <h3>使用遇到问题？</h3>
      <div class="mt20">
        <p>输入充值金额后，为什么无法成功充值？</p>
        <p class="mt10 infoFont">答：通过微信、支付宝线上扫码充值，会受到充值额度的限制，建议你分批进行充值，每次充值金额不超过10000元；</p>
        <p class="infoFont">通过线下转账充值，金额没有受限制，需要正确填写你当时对公打款至筷赚公司的付款金额。</p>
        <p class="infoFont">例如：某商家分次给筷赚打款10000元和5800元。该商家需要店铺余额充值填写两次付款信息，金额分别是10000元和5800元。如果填写金额合计数15800元，系统审核失败。</p>
      </div>
      <!-- <div class="mt20">
        <p>充值成功了，为什么收支明细里没有记录？</p>
        <p class="mt10 infoFont">答：充值金额是实时到账的，可到资产-对账单-对账单明细中查询充值记录，若无查到记录可能是系统延时引起的，你可稍后查看。若超过一天仍未显示，请与筷赚客服联系。</p>
      </div> -->
    </el-card>
    <el-dialog title="收银台" :visible.sync="rechargeShow" width="30%" >
      <el-form ref="form" :model="form" label-width="100px">
        <el-form-item label="商家名称：">
          <div>{{shop.shopName}}</div>
        </el-form-item>
        <el-form-item label="账户名称：">
          <div>店铺余额</div>
        </el-form-item>
        <el-form-item label="充值金额：">
          <div>{{rechargeForm.money}}</div>
        </el-form-item>
        <el-form-item label="支付方式：">
          <div>扫码支付</div>
        </el-form-item>
        <el-form-item label="二维码：">
          <div id="qrcode"></div>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="rechargeShow = false">取 消</el-button>
        <el-button type="primary" @click="rechargeShow = false">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
import { mapState } from 'vuex'
import QRCode from 'qrcodejs2';
import { getPrePayConfig , getShopAsset } from '@/api/assetsManage/verifyFund'
export default {
  data(){
    return{
      form:{
        resource:'1'
      },
      rechargeForm:{},
      rechargeShow:false,         // 充值弹出框
      shopAsset:0
    }
  },
  created(){
    this.rechargeForm.shopId = this.shop.shopId
    this.rechargeForm.operateId = this.user.userId
    this.getData()
  },
  methods:{
    getData(){
      getShopAsset(this.shop.shopId).then(res=>{
        console.log('默认数据',res)
        if(res.code == 200){
          this.shopAsset = res.data.shopAsset;
        }
      })
    },
    // 点击充值
    onSubmit(){
      if(!this.rechargeForm.money){
        this.$message.error('请输入金额后再试');
        return;
      }
      this.getCode()
      this.rechargeShow = true;
    },
    getCode(){
      console.log('this.rechargeForm',this.rechargeForm)
      getPrePayConfig(this.rechargeForm).then(res=>{
        console.log('获取支付二维码',res)
        this.qrcode = new QRCode('qrcode',{
          width:150,
          height:150,
          text:res.data,
          colorDark:'#000',
          colorLight:'#fff'
        })
      })
    },
  },
  computed: {
    ...mapState([
      'user',
      'shop'
    ])
  },
}
</script>
<style lang="less" scoped>
.fs40{
  font-size: 40px;
  font-weight: bold;
}
</style>